/* Медиа-запросы */
@mixin mobile {
  @media (max-width: #{$mobile-breakpoint - 1px}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$mobile-breakpoint}) and (max-width: #{$tablet-breakpoint - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$tablet-breakpoint}) {
    @content;
  }
}

/* Хелперы */
@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

// Section helpers
@mixin section-padding {
  padding: $spacing-xl * 2 $spacing-xl;
  
  @include mobile {
    padding: $spacing-xl $spacing-md;
  }
  
  @include tablet {
    padding: $spacing-xl * 1.5 $spacing-lg;
  }
}

@mixin grid-responsive {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: $spacing-xl;
  
  @include mobile {
    grid-template-columns: 1fr;
    text-align: center;
  }
  
  @include tablet {
    grid-template-columns: 1fr;
    gap: $spacing-xl * 2;
  }
}

@mixin image-responsive {
  width: 100%;
  max-width: 100%;
  height: auto;
  
  @include mobile {
    max-width: 100%;
  }
}

@mixin title-responsive {
  font-weight: 700;
  margin-bottom: $spacing-lg;
  line-height: 1.1;
  
  @include mobile {
    font-size: 36px !important;
    margin-bottom: $spacing-md;
  }
  
  @include tablet {
    font-size: 56px !important;
  }
}

@mixin description-responsive {
  font-weight: 500;
  line-height: 1.5;
  
  @include mobile {
    font-size: 16px !important;
  }
  
  @include tablet {
    font-size: 20px !important;
  }
}