@import '../../assets/styles/root';
@import '../../assets/styles/mixins';
@import '../../assets/styles/animations';

.slider {
  position: relative;
  min-height: 80vh;
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 80px 0;
  background: #ffffff;

  @include mobile {
    min-height: 60vh;
    padding: 60px 0;
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 1600px;
    margin: 0 auto;
    padding: 0 20px;

    @include mobile {
      padding: 0 10px;
    }
  }

  &__slider-wrapper {
    position: relative;
    width: 100%;
    max-width: 1500px;
    z-index: 1;

    .slider-swiper {
      width: 100%;
      padding: 60px 0 80px;

      @include mobile {
        padding: 40px 0 60px;
      }

      .swiper-slide {
        height: 281px;

        @include tablet {
          height: 225px;
        }

        @include mobile {
          height: 180px;
        }
      }
    }

    .slider__slide {
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: 16px;
      overflow: hidden;
      box-shadow:
        0 12px 30px rgba(0, 0, 0, 0.12),
        0 0 0 1px rgba(0, 0, 0, 0.06);
      background: #f8f9fa;
      transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      cursor: pointer;

      @include mobile {
        border-radius: 12px;
      }

      &:hover {
        transform: translateY(-12px) scale(1.02);
        box-shadow:
          0 30px 60px rgba(0, 0, 0, 0.25),
          0 0 0 1px rgba(0, 0, 0, 0.12);

        @include mobile {
          transform: translateY(-4px);
        }

        .slider__slide-image {
          transform: scale(1.08);
        }
      }

      &-image-container {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: linear-gradient(135deg, #e9ecef 0%, #dee2e6 100%);
      }

      &-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
        display: block;
      }
    }

    // Кнопки навигации
    .slider__nav-button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 52px;
      height: 52px;
      background: rgba(255, 255, 255, 0.95);
      backdrop-filter: blur(10px);
      -webkit-backdrop-filter: blur(10px);
      border-radius: 50%;
      border: 1px solid rgba(0, 0, 0, 0.08);
      cursor: pointer;
      z-index: 10;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 6px 20px rgba(0, 0, 0, 0.12);
      transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
      color: #1a202c;

      svg {
        transition: transform 0.3s ease;
      }

      &:hover {
        background: #ffffff;
        transform: translateY(-50%) scale(1.15);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.18);
        border-color: rgba(0, 0, 0, 0.12);

        svg {
          transform: scale(1.1);
        }
      }

      &:active {
        transform: translateY(-50%) scale(1.05);
      }

      &--prev {
        left: -70px;

        @include tablet {
          left: -50px;
        }

        @include mobile {
          left: 5px;
        }
      }

      &--next {
        right: -70px;

        @include tablet {
          right: -50px;
        }

        @include mobile {
          right: 5px;
        }
      }

      @include tablet {
        width: 48px;
        height: 48px;
      }

      @include mobile {
        width: 40px;
        height: 40px;
        background: rgba(255, 255, 255, 0.9);

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }

    // Пагинация
    .swiper-pagination {
      bottom: 30px !important;

      @include mobile {
        bottom: 20px !important;
      }

      &-bullet {
        width: 10px;
        height: 10px;
        background: rgba(0, 0, 0, 0.25);
        opacity: 1;
        transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
        margin: 0 6px !important;

        @include mobile {
          width: 8px;
          height: 8px;
          margin: 0 4px !important;
        }

        &:hover {
          background: rgba(0, 0, 0, 0.4);
          transform: scale(1.3);
        }

        &-active {
          background: #1a202c;
          transform: scale(1.5);
          box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);

          @include mobile {
            transform: scale(1.3);
          }
        }
      }
    }
  }
}